@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Lato);


#sonogram {
    background-color: white;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 15% 0%;
    /*position: relative;*/

}

#sonogramImg {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 50em;
    width: 100%;
}

.credits {
    font-size: 0.8em;
    font-family: "Lato", sans-serif;
}
#idenInfo {
    text-align:center;
    display: flex;
    flex-direction: column;
    margin: 0em 2em;
}

#audioControls {
    position: relative;
    top: -3em;
}

#playPauseButton{
    height: 100%;
    width: 25%;
    max-width: 125px;
    font-size: 3em;
    border-radius: 50%;
    background-color: lightgreen;
    border: none;
    outline: none;
    position: relative;
    top: 0%;
    left: .1em;
}

#playPauseButton:before {
    background-color: blueviolet;
    content: "";
    float: left;
    padding-top: 100%;
}

svg {
    width: 100%;
    height: auto;
} .pause {
    position: absolute;
    left: -1%;
    top: -3%;
  }
  .play {
      position: absolute;
      left: 4%;
      top: -2%;
  }

#userGuess {
    margin: 0em 2em;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    height: auto;
    position: relative;

} label {
    display: block;
    font-size: 2em;
    margin: .5em;
    font-family: "Open Sans", sans-serif;
    font-weight: bold;
  }
 input {
     /*background-color: red;*/
    width: 100%;
    display: block;
    font-size: 1em;
}

 #cheatButton {
     position: absolute;
     border: none;
     background-color: #dedede;
     font-weight: bold;
     border-radius: 5px;
     height: 2.5em;
     top: 0;
     right: 0;
 }

 #nextBirdButton {
     border: none;
     background-color: #009933;
     color: white;
     font-weight: bold;
     font-size: 1.2em;
     border-radius: 5px;
     padding: .5em 1em;
 }

 .guessDiv {
     border: 2px solid red;
 }

form {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

 .textInput {
     font-size: 1.5em;
     width: 100%;
     font-family: "Lato", sans-serif;
 }

 .submitInput {
     width: 30%;
     border: none;
     margin: 1em;
     padding: .5em;
     background-color: lightgreen;
     font-weight: bold;
     border-radius: 5px;
 }

#feedback {
    font-size: 1.5em;
    position: relative;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    margin: .4em;
}

.incorrect {
    color: #cc0000;
}

.correct {
    color: #009933;
}
.App {
    height: 100vh;
    width: 100vw;
}

.identifier {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.loading {
    align-items: center;
    height: 100%;
    font-size: 3em;
    font-family: "Open Sans", sans-serif;
}

body {
    background-color: #fafafa;
}
